@import url(https://fonts.googleapis.com/css?family=Cabin);
html {
  box-sizing: border-box;
  background-color: #2c3443;
  font-family: 'Cabin', sans-serif;
  background-color: #2c3443;
}


#root {  
  text-align: center;
  background-color: #b1cdce;
}

#root h2 {
  font-size: 3em;
  color : #b1cdce;
  background-color: #2c3443;
  margin: 2em 0 0.15em 0; 
  font-weight: bold;
  padding-bottom: 3%; 
}


.rules {
  margin:1em auto 0.8em auto;
  max-width: 43%;
  
}

.rules h3 {
  font-size: 2em;
  margin: 5px 0 0 0;
  color: #0d88a8;
  font-weight: bold;
  cursor: pointer;
}

.rules p {
  text-align: justify;
  text-align-last: center;
  margin: 5px 1em;
  font-size: 0.92em;
  color: #0d88a8
}



.colors .color-holder, .board .peg {
  display: inline-block;
  width: 40px;
  height: 40px;
  border: 1px dashed #2c3443;
  margin-right: 8px;
  border-radius: 21px;
  cursor: pointer;
  background-color: #bdd9da;
} 

.colors .red, .circles .red {
  background-color: #E42244;
}

.colors .green, .circles .green {
  background-color: #70C030;
}

.colors .orange, .circles .orange {
  background-color: #FFBB36;
}

.colors .blue, .circles .blue {
  background-color: #36BEDD;
}

.colors .purple, .circles .purple {
  background-color: #9F66DF;
}

.colors .active {
  border: 2px solid #2c3443;
  border-radius: 21px;
}

.board {
  margin-top: 15px;  
  margin-bottom: 2%;
  padding-bottom: 5%;
}

.board div {
  display: inline-block;
  margin-right: 5px;
  vertical-align: bottom;
  margin-bottom: 1%;
}

.board .circles {
  margin-left: 5px;
}

.board .ok-button {    
  font-size: 1em;
  padding: 16px 0;  
  color: #2c3443;
  margin-right: 12px;
  cursor: pointer;

}


.board .ok-button.disabled {
  color: #8d8796b4;
  cursor: default;
}

.board .ok-button.hidden {
  display: none;
  cursor: default;
}

.board .row {
  display: block;
 
  margin: 0 auto 3px auto;
  max-width: 360px;
}

.board .row.active {
  border:2px solid #0d88a8;
  border-radius: 25px;
  background-color: #a0bbbb;
}

.board .hints {
  margin-right: 0;
} 

.board .hints span {
  display: inline-block;
  width: 8px;
  height: 8px;
  border: 1px solid #777;
  background-color: #F0EAED;  
  margin: 15px 2px;
  font-size: 0.75em;
  padding: 0;
}

.board .hints span.exact {
  background-color: #02b12b;  
}

.board .hints span.partial {
  background-color: #DDBB22;  
}

.solution p {
  margin-top: 3%;
  margin-bottom: 1%;
}

.solution .hidden {
  display: none;
}

.solution .color-holder {
  cursor: default;
}

.solution p {
  color: #0d88a8;
  font-size: 1.5em;
  cursor: default;
  text-decoration: none;
  padding-bottom: 3%;
  font-weight: bold;
  font-size: 2rem;
  max-width: 25%;
  margin: auto;
}

.msg {
  margin: 0.5em auto;
  color: #0d88a8;
  font-size: 3.1em;
}

.newGame {
 margin-bottom: 5%;
 margin-top: 2%;
 color: #0d88a8;
 font-size: 2rem;

}

.newGame:hover {
  background-color: rgb(248, 219, 124);
}

